import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';

import styles from './Table.scss';


class Table extends PureComponent {

	static propTypes = {
		className: PropTypes.string,
		title: PropTypes.string,
		description: PropTypes.string,
		data: PropTypes.object,
		color: PropTypes.string,
	}

	renderHeader = header => {
		return <div className={styles('header')}>{header.name}</div>;
	}

	renderRows = (row, index) => {
		return (
			<div key={`${row.name}-${index}`} className={styles('row')}>
				{row.cells.map(this.renderCell)}
			</div>
		);
	}

	renderCell = (cell, index) => {
		return (
			<div key={`${cell.name}-${index}`} className={styles('cell-container')}>
				<div className={styles('cell')}>{cell.name}</div>
				<div className={styles('subcell')}>{cell.value}</div>
			</div>
		);
	}

	renderColumns = data => {
		data.headers.forEach((header, index) => {
			const cells = data.rows.map(row => {
				return row.cells[index];
			});
			console.log(cells)
			return (
				<div key={`${header.name}-${index}`} className={styles('column')}>
					{this.renderHeader(header)}
					{cells && cells.map(this.renderCell)}
				</div>
			);
		});
	}

	render() {
		const {
			className,
			title,
			description,
			data,
		} = this.props;

		return (
			<div className={classnames(styles('container'), className)}>
				{title && <h3 className={styles('title')}>{title}</h3>}
				<div className={styles('table-container')}>
					{this.renderColumns(data)}
				</div>
			</div>
		);	
	}
}

export default Table;
